
.card {
    background: rgb(248, 246, 246);
    border-radius: 2px;
    height: 250px;
    margin: 1rem;
    margin-top: 10px;
    position: relative;
    width: 280px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background:rgb(243, 242, 242);
  }
  .card > .img-container > img {
    width: 100%
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }

